  --text-dark-color: #000;
  --dominate-color: #fffeff;
  --seconday-color: #006992;
  --accent-color: #eca400;
}

.sidenav {
  grid-column: 1/2;
  grid-row: 1/2;
  display: flex;
  flex-direction: column;
  border-right: 2px solid #aaa;
  justify-items: center;
  align-items: center;
}

.projectlist {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

