___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --text-dark-color: #000;
  --dominate-color: #fffeff;
  --seconday-color: #006992;
  --accent-color: #eca400;
}

.todocontent {
  grid-column: 2/3;
  grid-row: 1/2;
  overflow: hidden;
}

.todocontent .todolistofselectedproject {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.todocontent .todolistofselectedproject .todo {
  min-width: 100%;
  max-height: 60px;
  display: flex;
  flex-direction: row;
  color: var(--dominate-color);
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
  margin: 0.8rem 0;
  background-color: var(--seconday-color);
}

.todocontent .todolistofselectedproject .todo p {
  font-size: large;
  margin: 0 20rem;
}

.todocontent .todolistofselectedproject .todo button {
  background-color: var(--accent-color);
  color: var(--dominate-color);
  min-height: 100%;
  align-self: stretch;
  display: flex;
  flex-wrap: nowrap;
  justify-items: center;
  align-items: center;
  margin: 0;
  border: 4px solid var(--accent-color);
  border-left: 0;
  float: right;
}

.todocontent .todolistofselectedproject .todo button svg {
  margin-left: 1rem;
  transition: stroke ease-in-out 300ms;
}

.todocontent .todolistofselectedproject .todo button:hover svg {
  stroke: var(--accent-color);
}

.todocontent .todolistofselectedproject .todo button:hover {
  color: var(--accent-color);
  background-color: var(--dominate-color);
}

.todocontent .todolistofselectedproject .todo h1 {
  float: left;
  margin-left: 3rem;
  font-size: 2.3rem;
}

@media (width<=720px) {
  .todocontent {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .todocontent .todolistofselectedproject .todo p {
    font-size: large;
    margin: 0;
  }
}
